import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

function ItemAdd() {
    const history = useHistory();
    const [data, setData] = useState({
        title: "",
        quantity: "",
        price: "",

    });
    const submit = (e) => {
        e.preventDefault();

        let items;
        if (localStorage.getItem('items') === null) {
            items = [];
        }
        else {
            items = JSON.parse(localStorage.getItem('items'));
        }
        items.push(data);
        localStorage.setItem('items', JSON.stringify(items));
        // console.log(item);
        history.push('/');

    }
    const handle = (e) => {
        const new_data = { ...data };
        new_data[e.target.id] = e.target.value;
        setData(new_data);

    }
    return (
        <>
            <div className="addItem">
                <form onSubmit={e => submit(e)} className="form" autoComplete="off">
                    <div>
                        <label>Title</label>
                        <input onChange={handle} id="title" value={data.title} type="text" placeholder="Vegitable" />
                    </div>
                    <div>
                        <label>Quantity </label>
                        <input onChange={handle} id="quantity" value={data.quantity} type="number" placeholder="10" />
                    </div>
                    <div>
                        <label>Price</label>
                        <input onChange={handle} id="price" value={data.price} type="number" placeholder="6.99" />
                    </div>
                    <button>Add Item</button>
                </form>
            </div>
            {/* <Item data={data} /> */}
        </>
    )
}

export default ItemAdd
